@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";

.container-outer {
    max-width: 1140px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}
.container-inner {
    max-width: 960px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}
.news-page {

    .news-list {
        padding: 0;
    }
    .news-list__item {
        display: grid;
        grid-template-columns: minmax(256px, 400px) auto;
        align-items: flex-start;
        column-gap: 40px;
        padding-top: 34px;
        border-top: 1px solid #D9D9D9;
        img {
            border-radius: 5px;
            width: inherit;
            object-fit: cover;
        }
        &:not(:last-child) {
            margin-bottom: 27px;
        }
    }
    .news-list__item__right {

    }
    .news-list__item__name {
        font-family: 'PF Agora Sans Pro';
        font-style: normal;
        font-weight: 900;
        font-size: 14px;
        line-height: 24px;
        /* or 171% */
        text-transform: uppercase;
        color: #FF9900;
        margin-bottom: 16px;
    }
    .news-list__item__desc {
        font-family: 'PF Agora Sans Pro';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 26px;
        /* or 162% */
        color: #575757;
        margin-bottom: 28px;
    }
    .news-list__item__detail {
        font-family: 'PF Agora Sans Pro';
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 21px;
        /* identical to box height, or 160% */
        color: #007CA9;
        text-decoration: none;

        display: inline-block;
        background: #D8F4FF;
        border: 1px solid #5AB2D2;
        border-radius: 46px; 
        padding: 5px 15px;   
    }
}
.post-page {
    color: #FF9900;
    max-width: 667px;
    margin: 0 auto;
    h1 {
        font-family: 'PF Agora Sans Pro';
        font-style: normal;
        font-weight: 900;
        font-size: 25px;
        line-height: 43px;
        
        /* or 172% */
        text-transform: uppercase;
        margin-bottom: 48px;
    }
    p {
        font-family: 'PF Agora Sans Pro';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 26px;
        
        /* or 162% */
        
        color: #575757;       
    }
    h2 {
        font-weight: 900;
        font-size: 18px;
        line-height: 43px;    
        text-transform: uppercase;
        margin-bottom: 12px;    
    }

    .post-abs {
        position: absolute;

        &:nth-child(even) {
            right: 0;
            top: 50%;
        }
        &:nth-child(odd) {
            left: 0;
        }

        &:nth-child(3) {
            top: 80%;
        }
    }
}
@media screen and (max-width: 972px) {
    .post-page .post-abs {
        display: none;
    }
}
@media screen and (max-width: 768px) {
    .news-list__item {
        grid-template-columns: 1fr;
        gap: 28px 0;
    }
}
@media screen and (max-width: 375px) {
    .news-list__item img {
        width: 100%;
    }
}